
















import { Component, Vue, Prop, Emit } from "vue-property-decorator";

@Component({})
export default class Elmmenu extends Vue {
  @Prop() private path!: string;
  @Prop() private formtitle!: any;
  loading = false;

  @Emit("put")
  change() {}
  async submitForm() {
    this.loading = true;
    const res = await this.$http.put(
      `/${this.path}/${this.formtitle._id}`,
      this.formtitle
    );
    if (res.status === 200) {
      this.$message({
        showClose: true,
        message: "修改成功",
        type: "success"
      });
      this.change();
    } else {
      this.$message({
        showClose: true,
        message: "修改失败,请重新提交",
        type: "error"
      });
    }
    this.loading = false;
  }
  mounted() {}
}
